import Request from '@/common/js/request/index.js'
import {
	webconfig
} from '@/static/js/webconfig.js'
const api = new Request();
const getTokenStorage = () => {
	let token = ''
	try {
		token = uni.getStorageSync('token')
	} catch (e) {}
	return token
}
let isLogout = false;//
let isLoad = false;//loading
let confirm_show=false;  //



api.setConfig((config) => { /*  */
	config.baseUrl = webconfig.api_url
	config.header = {
		...config.header,
		Authorization: getTokenStorage()
	}
	return config
})

api.interceptor.request((config, cancel) => { /*  */
	config.header = {
		...config.header,
		Authorization: getTokenStorage()
	}
	if (config.url.indexOf('/order/') > -1 || config.url.indexOf('/payInfo/') > -1 || config.url.indexOf('/express/') >
		-1) {
		config.baseUrl = webconfig.api_url2
	} else if (config.url.indexOf('/sysDict/') > -1) {
		config.baseUrl = webconfig.api_url3
	}

	if (!isLoad && config.url.indexOf('/o2oShopingCart/insert2Cart') < 0 && config.url.indexOf(
			'/o2oShopingCart/getEffectiveGoodsNum') < 0) { //loading
		isLoad = true;
		uni.showLoading({
			title: ''
		});
	}
	/*
	if (!token) { // tokencancel catch() 
	  cancel('token ') // catch((err) => {}) err.errMsg === 'token '
	}
	*/
	// console.log(config);
	return config
})

// 
api.interceptor.response(async (response) => { /*  */
	if (isLoad) {//loading
		uni.hideLoading()
		isLoad = false;
	}
	if (response.data.code){// 200reject()
		// 
		if(response.data.code == 401 && !isLogout){
			isLogout = true;
			uni.reLaunch({
				url: '/pages/login/login_wechat'
			})
			setTimeout(function() {
				isLogout = false; //
			}, 1000)
		}
		// 
		if(response.data.code !== 401&&response.data.code !== 200&&!confirm_show){
			confirm_show = true;
			uni.showModal({
			    title: '',
			    content: response.data.msg||'',
				showCancel:false,
			    success: function (res) {
					 confirm_show = false;
			    }
			});
		}
	}
	return response.data || {};
}, (response) => { // 
	if (isLoad) {//loading
		uni.hideLoading()
		isLoad = false;
	}
	if (response.statusCode == 401) {
		if (!isLogout) {
			isLogout = true;
			uni.reLaunch({
				url: '/pages/login/login_wechat'
			})
			setTimeout(function() {
				isLogout = false; //
			}, 1000)
			return
		}

	} else {
		uni.showModal({
			title: '',
			content: response.errMsg,
			showCancel: false
		})
	}
	console.log(response);
	return response
})

export {
	api
}
